body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #000;
  color: #fff;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  position: relative;
}

.mafs-container {
  width: 600px;
  height: 600px;
  border: 2px solid #fff;
  margin-bottom: 20px;
  position: relative;
}

.complex-line {
  position: absolute;
  top: 10px;
  left: 10px;
}

.slider-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  position: absolute;
  top: 0;
  right: -319px;
  height: 100%;
  justify-content: center;
}
.slider-labels {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.play-pause-button {
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
}

.slider {
  width: 300px;
  height: 258px;
  writing-mode: bt-lr;
  transform: rotate(90deg);
  margin: 10px 0;
}

.timeline-slider-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.timeline-controls {
  display: flex;
  align-items: center;
  margin-top: 10px;
}



.timeline-controls {
  display: flex;
  align-items: center;
  margin-top: 10px;
  position: relative; /* Ensure relative positioning for absolute positioning */
}





.timeline-slider {
  width: 1080px; /* Adjust width as per your design */
  margin: 0 10px;
}

.time-labels {
  display: flex;
  justify-content: space-between;
  width: 1052px;
  margin-bottom: 5px;
  background-color: #080707;
}
